#contact {
    display: flex;
    flex-direction: column;
    background: linear-gradient(0deg, #FDEECE 50%, #FFFFFF);

}

#contactBottom {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  height: 700px;
  margin-top: 200px;
}

#smiley {
  width: auto;
  height: 225px;
  display: flex;
  z-index: 800;
  position: relative;
  margin-top: 250px;
}

#goodbye {
  display: flex;
  position: relative;
  z-index: 900;
  font-size: 300px;
  width: auto;
  color: rgb(184, 226, 240, 0.9);
  transform: scale(-1, 1);
}

#goodbyeDialogue {
  font-family: "Karla Regular", sans-serif;
  font-size: 22px;
  max-width: 18ch;
  position: absolute;
  color: white;
  z-index: 950;
}

#goodbyeSpeech {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 400px;
  margin-left: 30px;
  margin-right: 30px;
  margin-top: 20px;
  margin-bottom: 90px;
}

#socials {
    display: flex;
    justify-content: center;
    position: absolute;
    align-self: center;
    height: 250px;
    width: 500px;
    margin-top: 200px;
}


  .text {
    background-color: #FAFAFA;
    color: #000000; 
    word-wrap: break-word;
    align-self: center;
    font-family: Helvetica, sans-serif;
    font-size: 16px;
    max-width: 300px;
    margin-bottom: 12px;
    line-height: 24px;
    position: relative;
    padding: 10px 20px;
    border-radius: 25px;
  }


  #firstText {
    margin-top: 170px;
    margin-bottom: 0px;
    align-self: flex-start;
  }


  #secondText {
    margin-top: 5px;
    align-self: flex-start;
  }

  .text::before, ::after {
    content: "";
		position: absolute;
    bottom: 0;
    height: 25px;
  }

  #secondText::before {
		left: -7px;
    width: 20px;
    background-color: #FAFAFA;
		border-bottom-right-radius: 16px 14px;
  }

  #secondText::after {
    left: -26px;
    width: 26px;
    background-color:  #FCF4E2;
		border-bottom-right-radius: 10px;
  }


  #iMessage {
    display: flex;
    flex-direction: column;
    align-self: center;
  }


#meg {
    width: 150px;
    position: relative;
    align-self: center;
    margin-top: 100px;
    margin-bottom: 0px;
}

#mail {
    width: 90px;
    margin-top: 5px;
    
}

#linkedin {
    width: 100px;
    margin-top: 55px;
}

#github {
    width: 95px;
    margin-top: 10px;
}

#contactMe {
    font-family: Apparel-Bold, sans-serif;
    font-size: 22px;
    color: black;
    margin-top: 85px;

}


.zoom {
    transition: transform .2s; /* Animation */
    margin: 0 auto;
  }
  
  .zoom:hover {
    transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
    cursor: pointer;
  }

@font-face { 
    font-family: Apparel-Bold; src: url('../components/fonts/apparel-bold.otf'); 
} 

@font-face {
  font-family: Karla Regular; src: url('../components/fonts/Karla-Regular.ttf'); 
}


@media screen and (max-width:900px) {
  #meg {
    width: 120px;
  }

  #mail {
    width: 80px;
    margin-top: 0px;
  }

  #linkedin {
    width: 90px;
    margin-top: 30px;
  }

  #github {
    width: 85px;
    margin-top: 0px;
  }

  .text {
    font-size: 12px;
    padding: 3px 10px;
  }

  .text::before, ::after {
    content: "";
		position: absolute;
    bottom: 0;
    height: 20px;
  }

  #firstText {
    margin-top: 140px;
  }
  #secondText::before {
    background-color: #FAFAFA;
    width: 17px;
  }

  
  #secondText::after {
    background-color:  #FBF2DC;
    left: -25px;
  }

  #contactMe {
    font-size: 18px;
    margin-top: 70px;
  }

  #smiley {
    width: auto;
    height: 180px;
    margin-top: 130px;
    margin-bottom: 0px;
  }

  #goodbye {
    font-size: 250px;
  }

  #goodbyeDialogue {
    font-size: 18px;
  }

  #goodbyeSpeech {
    margin-top: 0px;
    margin-bottom: 130px;
  }

  #contactBottom {
    height: 500px;
  }
}


